
#bg {
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  min-width: 1200px;
  background: url("../assets/images/bg/6.jpg") no-repeat center center/cover;
}
#bg #bg-canvas {
  display: block;
  background-color: transparent;
}
#bg.loginbg {
  background: url("../assets/images/bg/2.jpg") no-repeat center center/cover;
}


