/*decorate*/
#decorate {
  position: relative;
  top: 1px;
  width: 100%;
  height: 200px;
  margin-top: -60px;
}
#decorate svg {
  display: block;
  width: 100%;
  height: 100%;
}
/*content*/
#content {
  overflow: hidden;
  position: relative;
  width: 100%;
  background: -webkit-gradient(linear, left top, right top, color-stop(0, rgba(255,255,255,0.8)), color-stop(5%, rgba(255,255,255,0.97)), color-stop(95%, rgba(255,255,255,0.97)), to(rgba(255,255,255,0.8)));
  background: linear-gradient(90deg, rgba(255,255,255,0.8) 0, rgba(255,255,255,0.97) 5%, rgba(255,255,255,0.97) 95%, rgba(255,255,255,0.8) 100%);
}
#content .content-main {
  position: relative;
  width: 1000px;
  padding-top: 70px;
  margin: 0 auto;
}
#content .content-main ul.content-main-left {
  width: 100%;
}
#content .content-main ul.content-main-left li {
  position: relative;
  top: 0;
  width: 100%;
  height: 300px;
  border-radius: 5px;
  margin: 0 auto 200px;
  background-color: #fff;
  -webkit-transition: top 0.3s, -webkit-box-shadow 0.3s;
  transition: top 0.3s, -webkit-box-shadow 0.3s;
  transition: box-shadow 0.3s, top 0.3s;
  transition: box-shadow 0.3s, top 0.3s, -webkit-box-shadow 0.3s;
}
#content .content-main ul.content-main-left li .pic {
  overflow: hidden;
  position: absolute;
  z-index: 2;
  top: -50px;
  left: 50px;
  width: 150px;
  height: 180px;
  border-radius: 8px;
  background: url("../../assets/images/bg/2.jpg") center center/cover;
}
#content .content-main ul.content-main-left li .title {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 150px;
  border-radius: 5px;
}
#content .content-main ul.content-main-left li .title .t-bg {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0.3)), color-stop(80%, rgba(255,255,255,0.6)), to(#fff));
  background: linear-gradient(rgba(255,255,255,0.3) 0%, rgba(255,255,255,0.6) 80%, #fff 100%);
}
#content .content-main ul.content-main-left li .title .t {
  position: relative;
  z-index: 2;
  margin: 7px 0 7px 250px;
  color: #fff;
  text-shadow: 1px 1px 3px #999;
}
#content .content-main ul.content-main-left li .title .t-1 {
  padding-top: 15px;
  font-size: 22px;
  font-weight: bolder;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#content .content-main ul.content-main-left li .title .t-1 a {
  color: #fff;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}
#content .content-main ul.content-main-left li .title .t-1 a:hover {
  color: #ddd;
}
#content .content-main ul.content-main-left li .article {
  position: relative;
  width: 92%;
  height: 150px;
  padding: 0 4%;
}
#content .content-main ul.content-main-left li .article p {
  overflow: hidden;
  width: 100%;
  height: 90px;
  text-indent: 2em;
  font-size: 16px;
  line-height: 30px;
  color: rgba(0,0,0,0.6);
  text-shadow: 1px 1px 1px #ddd;
}
#content .content-main ul.content-main-left li .article .more {
  position: absolute;
  left: 30px;
  bottom: 15px;
  cursor: pointer;
}
#content .content-main ul.content-main-left li .article .more a {
  display: block;
  color: #fff;
  padding: 5px 20px;
  height: 20px;
  line-height: 20px;
  background-color: #bebbaa;
  border-radius: 20px;
}
#content .content-main ul.content-main-left li .article .more a:hover {
  color: #ddd;
}
#content .content-main ul.content-main-left li .article .comment {
  position: absolute;
  right: 30px;
  bottom: 15px;
}
#content .content-main ul.content-main-left li .article .comment a {
  height: 30px;
  line-height: 30px;
  color: #999;
}
#content .content-main ul.content-main-left li .article .comment a span {
  color: #f00;
}
#content .content-main ul.content-main-left li.right .pic {
  left: auto;
  right: 50px;
}
#content .content-main ul.content-main-left li.right .title .t {
  margin-left: 80px;
}
#content .content-main ul.content-main-left li:hover {
  top: -2px;
  -webkit-box-shadow: 2px 2px 3px #999;
          box-shadow: 2px 2px 3px #999;
}
/*decorate2*/
#decorate2 {
  position: relative;
  width: 100%;
  height: 200px;
}
#decorate2 svg {
  display: block;
  width: 100%;
  height: 100%;
}


